import Page from 'components/Page';
import React from 'react';
import classnames from 'classnames';
import { Consumer } from "../components/Context";
import {
  TabContent, 
  TabPane, 
  Nav, 
  NavItem, 
  NavLink, 
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import states from "../api/states";
import PageSpinner from 'components/PageSpinner';
import { findBillingData, } from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import CustomToolbarSelectLoads from "../components/CustomToolbarSelectLoads";


class BillingAccountPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeTab:'1',
            validUntil : '',
            payment_id:'',
            xtoken:'',
            msg: '',
            error: '',
            preload:false,
            data:[],
            account:{},
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            findBillingData(obje_results.user.token, {group: "30"})
            .then((response) => {
                this.setState({
                    ...this.state, 
                    xtoken : obje_results.user.token,
                    account: response.account,
                    data: response.data,
                });
            }).catch((error) => console.log(error));
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    toggleTab = tab => {
      if( this.state.activeTab !== tab)
      {
        this.setState({...this.state, activeTab: tab});
      }
    }
    
    firstUpper = (str) => {
      let string = str.toLowerCase();
      return string.charAt(0).toUpperCase() + string.slice(1);
    }
    render() {
      return (
        <Consumer>
        {context => (
        <Page title="My Account" breadcrumbs={[{ name: 'Account', active: true }]}>
            <Card>
              <CardBody>
              <Nav tabs>
              <NavItem>
                <NavLink
                  className={classnames({ active: this.state.activeTab === '1' })}
                  onClick={() => { this.toggleTab('1'); }}
                >
                  Account information
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={classnames({ active: this.state.activeTab === '2' })}
                  onClick={() => { this.toggleTab('2'); }}
                >
                  Payment History
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={this.state.activeTab}>
              <TabPane tabId="1">
                {/* New entry */}
                <Row>
                  <Col xl={12} lg={12} md={12}>
                    <Row>
                      <Col xl={12} lg={12} md={12}>
                        <Card className="borderless">
                            {this.state.preload && (
                              <CardHeader><PageSpinner/></CardHeader>
                            )}
                          {this.state.error.length > 0 && (
                              <Alert color="danger">{this.state.error}</Alert>
                          )}
                          {this.state.msg.length > 0 && (
                              <Alert color="success">{this.state.msg}</Alert>
                          )}
                        </Card>
                      </Col>
                    </Row>
                        <Col xl={12} lg={12} md={12}>
                          <Card body className={"no-border"}>
                            <Form>
                                <FormGroup>
                                    <Label>Account Name:</Label>
                                    <Input readOnly type="text" value={this.state.account.company}></Input>
                                    <Label>Account ID:</Label>
                                    <Input readOnly type="text" value={this.state.account.account}></Input>
                                    <Label>Account Valid Until:</Label>
                                    <Input readOnly type="text" value={this.state.account.active_to}></Input>
                                </FormGroup>
                                <FormGroup>
                                <Button type="button" onClick={() => {
                                  window.location.href = '/bills/payment';
                                }}>Renew now</Button>
                                </FormGroup>
                            </Form>
                          </Card>
                        </Col> 
                  </Col>
                </Row>
                {/* end new entry */}
              </TabPane>
              <TabPane tabId="2">
                <Row>
                  <Col xl={12} lg={12} md={12}>
                    <Card body className={"no-border"}>
                    <Table responsive>
                        <thead>
                        <tr>
                            <th>#InvoiceNo.</th>
                            <th>Account</th>
                            <th>Amount</th>
                            <th>Payment Initiated</th>
                            <th>Payment Completed</th>
                        </tr>
                        </thead>
                        <tbody>
                        { this.state.data.map((trp, idx) => (
                            <tr>
                                <th>{trp.invoice}</th>
                                <th>{this.state.account.account}</th>
                                <td>${trp.amount}</td>
                                <td>{trp.updated_at}</td>
                                <td>{trp.created_at}</td>
                            </tr>
                        ))}
                        </tbody>
                    </Table>
                  </Card>
                  </Col>
                </Row>
              </TabPane>
            </TabContent>
              </CardBody>
            </Card>
        </Page>
        )}
        </Consumer>
      );
    }
}
export default BillingAccountPage;
