import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";
import EditIcon from '@material-ui/icons/Edit';
import Attachment from '@material-ui/icons/Attachment';
import DeleteIcon from '@material-ui/icons/Delete';
import PaymentIcon from '@material-ui/icons/Payment';
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";
import { withStyles } from "@material-ui/core/styles";
import states from "../api/states";
import { uploadLoadDocs, uploadAttachPay, deleteLoad, findDrivers,findTrucks } from "../api/api";
import {
  Button,
  ButtonGroup,
  FormGroup,
  Form,
  Label,
  Input,
  Card,
  CardBody,
  CardHeader,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Alert,
} from 'reactstrap';

const defaultToolbarSelectStyles = {
  iconButton: {
  },
  iconContainer: {
    marginRight: "24px",
  },
  inverseIcon: {
    transform: "rotate(90deg)",
  },
};

class CustomToolbarSelectLoads extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
        showEditModal:false,
        showPayModal:false,
        saved:false,
        id: '',
        bol:'',
        is_delivered : 0,
        delivery_docs : '',
        paid:'nn',
        xtoken:'',
        msg: '',
        error: '',
    };
  }
  hideEditModal = () => {
    this.setState({
      ...this.state,
      showEditModal:false,
      showPayModal:false,
    });
    if(this.state.saved)
    {
      this.props.rerenderParentCallback();
    }
  }

    handleDocInput = (event) => {
        const v = event.target.files[0];
        this.setState({...this.state, delivery_docs: v,});
    }
    handlePayStatusInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, paid: v,});
    }
    
    handleEditSaveChanges = () => {
        const formData = new FormData();
        formData.append('delivery_docs', this.state.delivery_docs);
        uploadLoadDocs(this.state.xtoken, formData, this.state.id)
        .then((response) => {
            // console.log('save error', response);
            if( response.status === 200)
            {
              this.setState({
                ...this.state,
                saved:true,
                error: '',
                msg: response.message,
              });
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
            });
            console.log('error', error);
        });
    }
    handlePaySaveChanges = () => {
      let postData = {status: this.state.paid};
      uploadAttachPay(this.state.xtoken, postData, this.state.id)
      .then((response) => {
          // console.log('save error', response);
          if( response.status === 200)
          {
            this.setState({
              ...this.state,
              saved:true,
              error: '',
              msg: response.message,
            });
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
          });
          console.log('error', error);
      });
  }
  handleUploadDeliveryDoc = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    this.setState({
        showEditModal:true,
        id: selectedRowData.id,
        bol : selectedRowData.bol,
        is_delivered : selectedRowData.is_delivered,
        xtoken:this.props.Xtoken,
    });
  }
  handleAttachPayment = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    this.setState({
        showPayModal:true,
        id: selectedRowData.id,
        bol : selectedRowData.bol,
        is_delivered : selectedRowData.is_delivered,
        xtoken:this.props.Xtoken,
    });
  }
  handleGoToEdit = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    window.location.href = '/manage-loads/edit/' + selectedRowData.id;
    /** got to path */
  }
  handleDeleteSelection = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    deleteLoad(this.props.Xtoken, selectedRowData.id)
    .then((response) => {
      if(response.status === 200 )
      {
        alert(response.message);
        this.props.rerenderParentCallback();
        return;
      }
      alert(response.message);
      return;
    })
    .catch((error) => {
      alert('Connection error occured');
    });
  };

  handleClickDeselectAll = () => {
    this.props.setSelectedRows([]);
  };
  firstUpper = (str) => {
    let string = str.toLowerCase();
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  handleClickBlockSelected = () => {
    console.log(`block users with dataIndexes: ${this.props.selectedRows.data.map(row => row.dataIndex)}`);
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.iconContainer}>

        <Tooltip title={"Attach Payment"}>
          <IconButton className={classes.iconButton} onClick={this.handleAttachPayment}>
            <PaymentIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Attach Delivery Docs"}>
          <IconButton className={classes.iconButton} onClick={this.handleUploadDeliveryDoc}>
            <Attachment className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Modify Load"}>
          <IconButton className={classes.iconButton} onClick={this.handleGoToEdit}>
            <EditIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Deselect row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickDeselectAll}>
            <IndeterminateCheckBoxIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Drop row"}>
          <IconButton className={classes.iconButton} onClick={this.handleDeleteSelection}>
            <DeleteIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        {/* modals */}
        <Modal
          size="lg"
          isOpen={this.state.showEditModal}
          backdrop={false}>
          <ModalHeader>
            Attach Signed Delivery Document
            <br></br>
            <small>This confirms that the Cargo was successfully delivered</small>
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
                <Col xl={6} lg={12} md={12}>
                  <Card>
                      <CardBody>
                      <Form>
                          <FormGroup>
                          <Label for="exampleEmail">Selected Load BOL*</Label>
                          <Input readOnly type="text" value={this.state.bol} name="bookdate"/>
                          </FormGroup>
                      </Form>
                      </CardBody>
                  </Card>
                </Col>
                <Col xl={6} lg={12} md={12}>
                  <Card>
                      <CardBody>
                      <Form>
                          <FormGroup>
                          <Label for="exampleEmail">Upload file*</Label>
                          <Input onChange={this.handleDocInput}
                              type="file" 
                              name="hjj"
                              placeholder=""
                          />
                          </FormGroup>
                      </Form>
                      </CardBody>
                  </Card>
                </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handleEditSaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
        
        {/* attach payment modal */}

        <Modal
          size="lg"
          isOpen={this.state.showPayModal}
          backdrop={false}>
          <ModalHeader>
            Mark Entry As Paid
            <br></br>
            <small>This confirms that the Cargo was successfully delivered and paid for</small>
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
                <Col xl={6} lg={12} md={12}>
                  <Card>
                      <CardBody>
                      <Form>
                          <FormGroup>
                          <Label for="exampleEmail">Selected Load BOL*</Label>
                          <Input readOnly type="text" value={this.state.bol} name="bookdate"/>
                          </FormGroup>
                      </Form>
                      </CardBody>
                  </Card>
                </Col>
                <Col xl={6} lg={12} md={12}>
                  <Card>
                      <CardBody>
                      <Form>
                          <FormGroup>
                            <Label for="exampleEmail">Payment Status*</Label>
                            <Input onChange={this.handlePayStatusInput} value={this.state.paid} defaultValue={this.state.paid} type="select" name="stt">
                              <option value="nn" disabled={true}>Select one</option>
                              <option value="1">Paid</option>
                              <option value="2">Awaiting Payment</option>
                            </Input>
                          </FormGroup>
                      </Form>
                      </CardBody>
                  </Card>
                </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handlePaySaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default withStyles(defaultToolbarSelectStyles, { name: "CustomToolbarSelectLoads" })(CustomToolbarSelectLoads);